import AppBar from '@mui/material/AppBar';
import Avatar from '@mui/material/Avatar';
import { green } from '@mui/material/colors';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import XinyinDlg from './xinyinDlg';
import IconButton from '@mui/material/IconButton';
import { GenerateWords32Icon, ImportWords32Icon } from './icons';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import {
  clusterSelector,
  setCluster,
  SolanaClusterType,
} from './rpc/solanaClusterSlice';
import { useDispatch, useSelector } from 'react-redux';

export default function MyAppBar({
  onAvatarClick,
}: {
  onAvatarClick: () => void;
}) {
  const dispatch = useDispatch();
  const solanaCluster = useSelector(clusterSelector);

  return (
    <AppBar position="static" sx={{ mb: 2 }}>
      <Toolbar>
        <Avatar sx={{ bgcolor: green[600] }} onClick={onAvatarClick}>
          Xy
        </Avatar>
        <Typography variant="h6" sx={{ ml: 1 }} flexGrow={1}>
          数字钱包
        </Typography>
        <XinyinDlg type="generate">
          {({ triggerOpen }) => (
            <IconButton sx={{ mr: 1 }} onClick={triggerOpen}>
              <GenerateWords32Icon />
            </IconButton>
          )}
        </XinyinDlg>
        <XinyinDlg type="import">
          {({ triggerOpen }) => (
            <IconButton sx={{ mr: 1 }} onClick={triggerOpen}>
              <ImportWords32Icon />
            </IconButton>
          )}
        </XinyinDlg>{' '}
        <FormControl size="small" sx={{ minWidth: 100 }}>
          <Select
            value={solanaCluster}
            onChange={(event) => {
              dispatch(setCluster(event.target.value as SolanaClusterType));
            }}
            sx={{
              fontSize: '0.75rem',
              height: 32,
              fontWeight: 'bold',
              '& .MuiSelect-select': {
                color: 'white',
                fontWeight: 'bold',
              },
              '& .MuiOutlinedInput-notchedOutline': {
                borderColor: 'rgba(144, 202, 249, 0.6)', // 柔和的浅蓝色
                borderWidth: 1,
              },
              '&:hover .MuiOutlinedInput-notchedOutline': {
                borderColor: 'rgba(144, 202, 249, 0.8)', // 悬停时稍微加深
              },
              '&.Mui-focused .MuiOutlinedInput-notchedOutline': {
                borderColor: '#90CAF9', // 聚焦时使用更亮的蓝色
                borderWidth: 2,
              },
              '& .MuiSvgIcon-root': {
                color: 'rgba(255, 255, 255, 0.8)', // 图标使用稍透明的白色
              },
            }}
            displayEmpty
          >
            <MenuItem
              value={SolanaClusterType.mainnetBeta}
              sx={{ fontSize: '0.75rem' }}
            >
              🌐 主网
            </MenuItem>
            <MenuItem
              value={SolanaClusterType.devnet}
              sx={{ fontSize: '0.75rem' }}
            >
              🧪 开发网
            </MenuItem>
            <MenuItem
              value={SolanaClusterType.testnet}
              sx={{ fontSize: '0.75rem' }}
            >
              🧪 测试网
            </MenuItem>
          </Select>
        </FormControl>
      </Toolbar>
    </AppBar>
  );
}
